@import "../../../base";

.userNav {
    align-self: stretch;
    
    display: flex;
    align-items: center;
    & > * {
        transition: all 0.5s;
        color:  var(--color-primary-dark);
        padding: 0 2rem;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;
    }

    & > *:hover {
        color:  var(--color-primary);
        background-color: var(--color-grey-light-2);
    }

    &__iconBox {
        position: relative;
    }

    &__icon {
        height: 2.25rem;
        width: 2.25rem;
        fill: var(--color-grey-dark-2);
    }

    &__notification {
        font-size: .8rem;
        height: 1.75rem;
        width: 1.75rem;
        border-radius: 50%;
        background-color: var(--color-primary);
        color: #fff;
        position: absolute;
        top: 1.5rem;
        right: 1.1rem;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__userPhoto {
        height: 3.75rem;
        border-radius: 50%;
        margin-right: 1rem;
    }

}